html {
  font-family: sans-serif;
  font-size: 12px;
  background: #ffffff;
  margin: 0;
}

body {
  width: 475px;
  font-size: 1.2rem;
  font-family: sans-serif;
}

.pcr-app .pcr-swatches {
  justify-content: center;
}

.outer-wrapper {
  overflow: auto;
  width: 100%;
}
.outer-wrapper .settings-input .pickr {
  height: 100%;
  width: 100%;
  min-width: 29px;
}
.outer-wrapper .settings-input .pickr .pcr-button {
  height: 100% !important;
  width: 100% !important;
}
.outer-wrapper .settings-input .save {
  height: 100%;
  width: 100%;
  min-width: 29px;
}
.outer-wrapper .settings-container {
  max-height: 400px;
  overflow-y: auto;
}
.outer-wrapper .settings-container .empty-notice {
  font-weight: bold;
  font-size: 14px;
  text-align: center;
  margin: 30px 0;
}
.outer-wrapper .settings-container .setting {
  border-bottom: 0.1rem dotted #cccccc;
}
.outer-wrapper .settings-container .setting .display-container {
  display: flex;
}
.outer-wrapper .settings-container .setting .display-container .col-10 {
  max-width: 86%;
  width: 86%;
  flex: 0 0 86%;
}
.outer-wrapper .settings-container .setting .display-container .col-1 {
  max-width: 7%;
  width: 7%;
  flex: 0 0 7%;
}
.outer-wrapper .settings-container .setting .display-container .display-labelUrl .row > div {
  overflow-wrap: break-word;
  padding: 0 3px;
}
.outer-wrapper .settings-container .setting .display-container .display-labelUrl .row > div:first-child {
  padding: 0 3px 0 15px;
}
.outer-wrapper .settings-container .setting .display-container .display-labelUrl .row > div:last-child {
  padding: 0 10px 0 3px;
}
.outer-wrapper .settings-container .setting .display-container .display-color {
  margin-right: 0;
}
.outer-wrapper .settings-container .setting .display-container .display-color .display-color-color {
  float: right;
}
.outer-wrapper .settings-container .setting .display-container .display-color div {
  height: 24px;
  width: 24px;
  border-radius: 2px;
}
.outer-wrapper .settings-container .setting .display-container .display-delete {
  height: 24px;
  width: 24px;
  text-align: right;
}
.outer-wrapper .settings-container .setting .edit-container .pickr {
  height: 100%;
  width: 100%;
  min-width: 29px;
}
.outer-wrapper .settings-container .setting .edit-container .pickr .pcr-button {
  height: 100% !important;
  width: 100% !important;
}
.outer-wrapper .settings-container .setting .edit-container .update,
.outer-wrapper .settings-container .setting .edit-container .cancel {
  height: 100%;
  width: 100%;
  min-width: 29px;
}